$text-color : #777;
$primary-color: #ad00ff;
$secondary-color: #56007f;
$hover-color: #c64cff;
$light-color: #f7f7f7;
$dark-color: #111;
$header-background: #0a0a0ae6;
$header-color: #FFF;
$footer-background: #111;
$footer-color: #FFF;
$font-size: 14px;
/* Mixin */
/* Set các giá trị với màn hình tương ứng */
$mobile-width: 480px;
$tablet-width: 768px;
$desktop-width: 1024px;

@mixin mobile {
    @media (max-width: #{$mobile-width - 1px}) {
        @content;
    }
}
@mixin tablet0 {
    @media (min-width: #{$mobile-width}) and (max-width: #{$tablet-width - 1px}) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: #{$tablet-width}) and (max-width: #{$desktop-width - 1px}) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: #{$desktop-width +1px}) {
        @content;
    }
}

