@import 'var';

// @import '../assets/images/logo-1.png';
.preview__header {
    font-size: 12px;
    height: 54px;
    background-color: #262626;
    z-index: 100;
    line-height: 54px;
    margin-bottom: 1px;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    .logo {
        display: inline-block;
        padding: 0 20px;

        a {
            display: inline-block;
            position: absolute;
            top: 18px;
            text-indent: -9999px;
            height: 18px;
            width: 152px;
            background: url('https://public-assets.envato-static.com/assets/logos/envato_market-a5ace93f8482e885ae008eb481b9451d379599dfed24868e52b6b2d66f5cf633.svg');
            background-size: 152px 18px;
        }
    }

    .btn_buy {
        display: inline-block;
        padding: 0 20px;
        margin: auto 0;

        a {
            box-shadow: 0 2px 0 #6f9a37;
            position: relative;
            background-color: #82b440;
            font-size: 14px;
            padding: 5px 20px;
            line-height: 1.5;
            color: white;
            box-sizing: border-box;
            display: inline-block;
            margin: 0;
            border: none;
            border-radius: 4px;
            font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
            text-align: center;
            text-decoration: none;
            cursor: pointer;
        }
    }

    @media screen and (max-width:568px) {
        .logo a {
            position: absolute;
            top: 20px;
            left: 15px;
            height: 14px;
            width: 118px;
            background-size: 118px 14px;
        }

        .btn_buy {
            padding: 0 10px;
        }
    }
}

.NavBar {
    height: 100px;
    padding: 0 50px;
    border-radius: 0;
    z-index: 99999;
    background-color: #0a0a0ae6;
    box-shadow: 0 3px 5px #ad00ff4d;
    transition: all .5s ease-in-out;
    position: fixed;
    // top: 0;
    right: 0;
    left: 0;
    top: 54px;

    .nav_container {
        width: 100%;
        display: flex;
        height: 100%;
        align-items: center;
        justify-content: space-between;

        a {
            text-align: left;
            margin: auto 0;

            img {
                max-height: 50px;
            }
        }

        .main_menu {
            iconify-icon {
                font-size: 30px;
                float: right;
                color: $primary-color;
                display: none;
                cursor: pointer;
            }
        }

        .main_nav {
            display: inline-flex;
            margin-left: auto;

            ul {
                margin: 0;

                li {
                    float: none;
                    height: 100px;
                    padding: 0 15px;
                    display: table-cell;
                    vertical-align: middle;
                    transition: all .5s ease-in-out;
                    margin: 0 auto;

                    a {
                        color: $header-color;
                        font-family: "Open Sans", sans-serif;
                        font-size: 14px;
                        font-weight: 300;
                        letter-spacing: 2px;
                        padding: 0;
                        line-height: 1;
                        letter-spacing: 1px;
                        white-space: nowrap;
                        padding-left: 0 !important;
                        padding-right: 0 !important;
                        text-transform: uppercase;
                        transition: all .5s ease-in-out;
                    }
                }
            }
        }

        @media screen and (max-width:1200px) {

            .main_nav {
                // display: none;
                // flex: 100%;
                background-color: $header-background;
                border-top: 1px solid #0000001a;
                border-bottom: 1px solid #0000001a;
                padding: 0;
                position: absolute;
                width: 100%;
                top: 70px;
                left: 0;
                transition: all .5s ease-in-out;

                ul {
                    width: 100%;
                    padding: 20px 0;

                    li {
                        float: left;
                        width: 100%;
                        padding: 20px 50px;
                        height: auto;
                    }
                }
            }

            .main_menu iconify-icon {
                display: block;
            }

            a {
                font-size: 16px;
            }
        }

        @include desktop {
            .main_menu {
                font-size: 22px;
            }
        }

    }

    @media screen and (max-width:1200px) {
        height: 70px;
    }
}

.home {
    background-image: url('../assets/images/slider1.jpg');
    position: relative;
   
    height: 600px;
    padding-top: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-repeat: no-repeat;

    .row {
        z-index: 10;

        h1 {
            width: 100%;
            margin: 0 auto;
            color: #FFF;
            font-family: "Open Sans", sans-serif;
            font-size: 100px;
            font-weight: 600;
            letter-spacing: 15px;
            text-transform: uppercase;
            margin-bottom: 0;
            position: relative;
            animation: fadeInLeft 2s;
            line-height: 1.2;
        }

        hr {
            width: 50px;
            height: 3px;
            background-color: $primary-color;
            margin: 30px auto;
            border: 0;
            position: relative;
        }

        h2 {
            width: 100%;
            color: #FFF;
            font-family: "Open Sans", sans-serif;
            font-size: 30px;
            font-weight: 300;
            letter-spacing: 20px;
            margin-bottom: 0;
            -webkit-animation: fadeInRight 2s;
            animation: fadeInRight 2s;
        }

        @media screen and (max-width:600px) {
            h2 {

                font-size: 25px;
                letter-spacing: 3px;
                bottom: 30%;
                margin-top: 0;
            }

            h1 {
                font-size: 40px;
                letter-spacing: 1px;
            }
        }
    }

    div .scroll-down {
        width: 40px;
        height: 40px;
        position: absolute;
        bottom: 20px;
        left: 0;
        right: 0;
        margin: auto;
        z-index: 2;
        color: $primary-color;
        font-weight: 400;
        transition: all .5s ease-in-out;

        animation: fadeInDown 1s infinite alternate;
    }

    .scroll-down::before {
        content: '';
        display: block;
        border-bottom: 5px solid $primary-color;
        border-right: 5px solid $primary-color;
        margin: auto;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 20px;
        width: 40px;
        height: 40px;


        transform: rotate(45deg);
    }

    @include tablet {
        .row {
            h1 {
                font-size: 60px;
                margin-top: 35px;
            }

            h2 {
                font-size: 40px;
            }

            hr {
                width: 80px;
            }
        }

    }

    @include tablet0 {
        .row {
            h2 {
                font-size: 30px;
                letter-spacing: 3px;
            }

            h1 {
                font-size: 50px;
                letter-spacing: 5px;
            }

            hr {
                width: 75px;
                margin: 25px auto;
            }
        }

        .scroll-down::before {
            width: 30px;
            height: 30px;
            bottom: 15px;
        }
    }

    @include mobile {
        .scroll-down::before {
            width: 20px;
            height: 20px;
            bottom: 10px;
        }
    }
}

