    .img_team {
        display: flex;
        flex-direction: row;
        //flex-wrap: wrap;
        flex-wrap: nowrap;
        padding: 0 12%;

        .img_team_main {
            flex: 33%;

            // width: 300px;
            height: 400px;
            margin: 0 40px;
            overflow: hidden;
            text-align: center;
            position: relative;
            box-sizing: border-box;
            // padding-left: 70px;
            // padding-right: 70px;
            // padding: 0 80px;
            width: 100%;
            // visibility: visible;
            // animation-duration: 1s;
            //  animation: fadeInLeft;

            .img_team_content {
                margin: 0 auto;
                position: relative;

                overflow: hidden;

                img {
                    max-width: 200%;
                    // width: 100%;
                    height: 400px;
                    text-align: center;
                    transition: all 2s ease-in-out;
                }

                .mask {
                    background-color: $primary-color;
                    opacity: 0;
                    transition: all .5s ease-in-out;
                    width: 100%;
                    height: 400px;

                    position: absolute;
                    overflow: hidden;
                    text-align: center;
                    top: 0;
                    // left: 50px;

