.contact {
    padding-top: 100px;
    padding-bottom: 200px;
    background-color: #fff;

    .about_text {
        padding: 100px 50px 50px 50px;

        h2 {
            margin-bottom: .5rem;
            font-weight: 200;
            color: black;
            line-height: 1.2;
            letter-spacing: 5px;
            font-size: 2rem;

            .color {
                color: $primary-color;
            }
        }

        hr {
            width: 50px;
            height: 3px;
            background-color: $primary-color;
            margin: 30px auto;
            border: 0;
            position: relative;
        }
    }

    .contact_title {
        padding: 0 12%;

        .main_ct_title {
            display: flex;
            flex-wrap: wrap;

            .cl_title {
                width: 33.33%;
                text-align: center;

                h4 {
                    font-size: 1.5rem;
                    margin-bottom: .5rem;
                    font-weight: 500;
                    line-height: 1.2;
                }

                p {
                    font-size: 12px;

                }

                &:last-child p {
                    color: $primary-color;
                }
            }
        }

        .form_contact {
            margin-top: 50px;

            form .row {
                display: flex;
                flex-wrap: wrap;

                input {
                    border: 0;
                    border-bottom: 1px solid rgba(30, 30, 30, 0.3);
                    padding: 15px 0;
                    margin: 0 20px;
                    transition: all .5s ease-in-out;
                    width: 43%;
                    text-align: left;

                    &::placeholder {
                        font-size: 15px;
                    }

                    &:focus-visible {
                        outline: none;
                    }
                }
            }

            form textarea {
                width: 100%;
                border: 0;
                border-bottom: 1px solid rgba(30, 30, 30, 0.3);
                padding: 15px;
                height: 200px;
                margin: 100px 0 50px 0;
                transition: all .5s ease-in-out;

                &:focus-visible {
                    outline: none;
                }

                &::placeholder {
                    font-size: 15px;
                }
            }

            button {
                width: 120px;
                float: right;
                margin-top: 10px;
                cursor: pointer;
                border-radius: 0;
                font-size: 16px;
                font-weight: 400;
                letter-spacing: 3px;
                text-transform: uppercase;
                padding: 10px 30px;
                background-color: transparent;
                color: $primary-color;
                border: 3px solid $primary-color;
                transition: all .5s ease-in-out;

                &:hover {
                    color: #FFF;
                    background-color: $primary-color;
                }
            }
        }
    }

    @media screen and (max-width:1023px) {
        .about_text {
            h2 {
                font-size: 25px;
            }

            hr {
                width: 80px;
            }
        }

        .contact_title {
            .main_ct_title {
                .cl_title {
                    h4 {
                        font-size: 18px;
                    }

                    p {
                        line-height: 1.8;
                    }
                }
            }

            .form_contact {
                form .row {
                    flex-wrap: nowrap;
                }
            }
        }
    }

    @media screen and (max-width:768px) {
        padding: 0;

        .about_text {
            padding: 50px;

            h2 {
                font-size: 20px;
            }

            hr {
                width: 75px;
                margin: 25px auto;
            }
        }

        .contact_title {
            .main_ct_title {
                .cl_title {
                    width: 100%;

                    h4 {
                        font-size: 16px;
                    }

                    p {
                        line-height: 1.8;
                    }
                }
            }

            .form_contact {
                padding: 0 12%;
                margin-bottom: 100px;
                form .row {
                    flex-wrap: nowrap;
                }

                form textarea {
                    padding: 0;
                    margin: 25px 0 50px 0;
                    ;
                }
            }
        }
    }

    @media screen and (max-width:480px) {
        .about_text {

            padding: 50px 50px 0 50px;

            h2 {
                font-size: 18px;
            }

            hr {
                width: 50px;
                margin: 20px auto;
            }
        }

        .contact_title {
            .main_ct_title {
                .cl_title {

                    h4 {
                        font-size: 15px;
                    }

                    p {
                        line-height: 1.5;
                    }
                }
            }

            .form_contact {
                padding: 0;

                form .row {
                    flex-wrap: wrap;

                    input {
                        width: 100%;
                        
                        margin: 20px 0;
                        
                    }
                }

                form textarea {
                    padding: 0;
                    //padding: 0 40px;
